html {
  height: 0;
}

nav button {
  text-transform: uppercase;
  background: none;
  border: none;
  height: 60px;
  font-family: Montserrat, Helvetica Neue, Helvetica, Arial, sans-serif;
  font-weight: bold;
  text-decoration: none;
  display: block;
  padding: 6px 12px !important;
}

nav button:hover {
  background: #203833;
  border: 1px solid #4c746c;
  border-radius: 10% / 50%;
  box-shadow: 1px 2px 5px #000;
}

nav button:active {
  background: #203833;
  border: 1px solid #4c746c;
  border-radius: 10% / 50%;
  box-shadow: inset 1px 2px 5px #000;
}

nav {
  flex-direction: row;
  justify-content: stretch;
  align-items: stretch;
  width: 100%;
  height: 60px;
  display: flex;
  position: static;
  background: #40685f !important;
}

#icoli {
  width: 50px;
  height: 50px;
}

body {
  background-color: #203833;
  background-image: url("../bosque.5663e821.png");
  background-position: top;
  background-size: 100%;
  background-attachment: fixed;
  margin: 0;
}

#content1 {
  color: #fff;
  background-color: #203833;
  padding-left: 50px;
}

